.content-wrap {
  @media screen and (max-width: 768px) {
    padding-left: 15px !important;
    padding-right: 15px !important;
  }
  width: 100%;
  position: relative;
  > img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    z-index: 33;
  }
  > div {
    position: relative;
    z-index: 99;
  }
}
